.nav {
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link {
    position: relative;
  text-decoration: none;
  color: $main-text-color;
  font-size: 22px;
  font-weight: 700;
  line-height: 22px;
  letter-spacing: 2px;

  &::after  {
    content: '';
    position: absolute;
    bottom: -8px;
    display: block;
    height: 1px;
    width: 100%;
    background-color: $main-text-color;

    transform: scale(0);
    transition: transform $effect-duration;
    transform-origin: left;
  }

  &:hover::after {
    transform: scale(1);
  }
  }
}
